import React from 'react';
import styled from 'styled-components';
import HomeScreen from './Components/HomeScreen';
import VideoCollection from './Components/VideoCollection';
import Context from './Context';
import { shuffleVideos } from './utils';

const Background = styled.div`
  display: flex;
  height: 100%;
  background-color: #2300e2;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

function App() {
  const storedSequences = [
    // { initials: 'ass', vidIds: [22, 94, 22] },
    // { initials: 'you', vidIds: [64, 93, 94] },
    { initials: 'Mgo', vidIds: [22] },
  ];

  const [clips, setClips] = React.useState(shuffleVideos(10));
  const [narrative, setNarrative] = React.useState([]);
  const [singleChannel, setSingleChannel] = React.useState(false);
  const [videoSequences, setVideoSequences] = React.useState(storedSequences);

  const end = clips === narrative;

  const context = {
    clips,
    setClips,
    narrative,
    setNarrative,
    done: end,
    singleChannel,
    setSingleChannel,
    videoSequences,
    setVideoSequences,
  };

  return (
    <Background>
      <Context.Provider value={context}>
        {singleChannel && <VideoCollection />}
        {!singleChannel && <HomeScreen />}
      </Context.Provider>
    </Background>
  );
}

export default App;
