import React from 'react';
import styled from 'styled-components';
import '../../node_modules/video-react/dist/video-react.css';
import { getClip } from '../utils';
import { displayFlex } from '../constants';

const Wrapper = styled.div`
    ${displayFlex}
    margin: auto;
    width: 40rem;
    height: 25rem;
    padding: 1rem;
    background: black;
    filter: drop-shadow(10px 5px 4px #180096);
`;

const Video = styled.video`
  height: inherit;
`;

const srcControl = (videoIndex, vidIds) => getClip(vidIds[videoIndex]).clip;

export default function VideoStream(props) {
  const { vidIds, sequenceIndex, setSequenceIndex } = props;
  const [videoIndex, setVideoIndex] = React.useState(0);

  const onEnded = () => {
    setVideoIndex(videoIndex + 1);
    if (videoIndex + 1 >= vidIds.length) {
      setSequenceIndex(sequenceIndex + 1);
      setVideoIndex(0);
    }
  };

  return (
    <Wrapper>
      <Video
        muted
        autoPlay
        poster="/assets/poster.png"
        onEnded={onEnded}
        src={srcControl(videoIndex, vidIds)}
      />
    </Wrapper>

  );
}
