import React, { useContext } from 'react';
import styled from 'styled-components';
import VideoInput from './VideoInput';
import EnterButton from './EnterButton';
import Context from '../Context';

const EndComponent = styled.div`
  width: 100%;
  max-width: 45rem;
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
`;

const Initials = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
`;

export default function End() {
  const {
    narrative, setClips, setNarrative, videoSequences,
  } = useContext(Context);

  const [initials, setInitials] = React.useState('   ');

  const enterVideoSequence = () => {
    console.log(narrative);
    videoSequences.push({ initials, vidIds: narrative });
    setClips(narrative);
    setNarrative([]);
  };

  return (
    <EndComponent>
      <Initials>
        <VideoInput index={0} initials={initials} setInitials={setInitials} />
        <VideoInput index={1} initials={initials} setInitials={setInitials} />
        <VideoInput index={2} initials={initials} setInitials={setInitials} />
      </Initials>
      <EnterButton enterVideoSequence={enterVideoSequence} />
    </EndComponent>

  );
}
