import React from 'react';
import styled from 'styled-components';
// import Context from '../Context';

const Input = styled.input`
    width: 100%;
    height: 4rem;
    max-width: 5rem;
    font-size: inherit;
    font-size: 3rem;
    color: blue;
    border-style: outset;
    border-color: red;
    border-width: 5px;
    text-align: center;
    margin: 1rem;

  :hover {
    border-color: yellow;
  }

  :focus {
    border-color: yellow;
    background: yellow;
  }
`;


export default function InputComponent(props) {
  // TODO: capitalize
  const { initials, setInitials, index } = props;

  const handleChange = (event) => {
    const newCharacter = event.target.value;
    const newInitials = initials.substr(0, index)
     + newCharacter + initials.substr(index + newCharacter.length);
    setInitials(newInitials);
  };

  return (
    <Input onChange={handleChange} maxLength="1" />

  );
}
