import React, { useContext } from 'react';
import styled from 'styled-components';
import WordArt from 'react-wordart';
import VideoStream from './VideoStream';
import { displayFlex } from '../constants';
import Header from './Header';
import Context from '../Context';

const Screen = styled.div`
  ${displayFlex}
  flex-direction: column;
`;

export default function VideoCollection() {
  // const videoSequences = [
  //   { initials: 'ass', vidIds: [22, 94, 22] },
  //   { initials: 'you', vidIds: [64, 93, 94] },
  //   { initials: 'day', vidIds: [3] },
  // ];

  const {
    videoSequences,
  } = useContext(Context);

  const [sequenceIndex, setSequenceIndex] = React.useState(0);

  // TODO: fix this memory leak
  return (
    <Screen>
      <Header />
      <VideoStream
        vidIds={videoSequences[sequenceIndex % videoSequences.length].vidIds}
        setSequenceIndex={setSequenceIndex}
        sequenceIndex={sequenceIndex}
      />
      <WordArt text={videoSequences[sequenceIndex % videoSequences.length].initials} theme="radial" fontSize={70} />
    </Screen>
  );
}
